.contact-container{
    height: 100%;
    display: flex;
    flex-direction: column;
    
    
    gap: 2rem;
    width: 100vw;
    padding: 5rem 2rem;
    background-color: #1C1C1C;
}

.contact-middle{
    width: 100%;
    height: 100%;
    padding: 0rem 5%;
    
    display: flex;
    flex-wrap: wrap;
    
}

.contact-middle-left{
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    
}

.contact-middle-left h1{
    font-family: 'Island Moments';
    letter-spacing: 2px;
    font-size: 3rem;
    color: rgba(209, 8, 8, 0.877);
}

.contact-middle-left p{
    color: rgb(223, 217, 217);
    font-size: 1.4rem;
    width: 80%;
}

.contact-icons{
    display: flex;
    align-items: center;
    gap: 2rem;
}

.contact-icon-deco{
    width: 2.5rem;
    height: 2.5rem;
    /* padding: 5px; */
    border-radius: 50%;
    /* background-color: rgba(194, 10, 10, 0.877); */
    border: 2px solid green;
    display: flex;
    align-items: center;
    justify-content: center;
}

.actual-icon{
    font-size: 1.3rem;
    border-radius: 50%;
    color: rgba(207, 200, 200, 0.897);
}

.icon-desc{
    font-size: 1.2rem !important;
}

.contact-middle-right{
    height: 25rem;
    background-color: rgba(131, 129, 129, 0.795);
    border-radius: 5px;
    padding: 2rem 1rem;
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    
}

.contact-middle-right input{
    padding: .5rem 1rem;
    border: none;
    outline: none;
}

.contact-middle-right textarea{
    resize: none;
    padding: 1rem 1rem;
    border: none;
    outline: none;
}

.contact-button{
    width: 40%;
    height: 3rem;
    padding: .5rem 0rem;
}

@media (max-width: 600px) {
    .contact-middle{
        display: flex;
        flex-direction: column;
        gap: 2rem;
    }
    .contact-middle-left {
        width: 100%;
    }

    .contact-middle-right {
        width: 100%;
    }

    .contact-middle-left p{
        
        width: 100%;
    }


    .contact-middle-right input,
    .contact-middle-right textarea {
        width: auto;
    }
}