.video-container{
    height: 100%;
    display: flex;
    flex-direction: column;
    
    
    gap: 2rem;
    width: 100vw;
    padding: 5rem 2rem;
    background-color: #1C1C1C;
}


.video-page{
    width: 100%;
    display: flex;
    
}

.display-video{
    width: 93%;
    height: 50vh;
    border: 1px solid rgba(224, 7, 7, 0.89);
}



.individual-videos {
    margin-top: 5%;
}

.video-part-display{
    grid-template-columns: repeat(3, 1fr) !important;
    grid-auto-rows: minmax(250px, 450px) !important;
}


.video-div{
    display: flex;
    position: relative;
    flex-direction: column !important;
}

.video-play {
    position: absolute;
    font-size: 4rem;
    color: rgba(192, 11, 11, 0.808);
    display: none; /* Initially hide the play button */
}

.each-video-title {
    position: absolute;
    bottom: 0%; /* Adjust the position as needed */
    left: 0%;
    width: 100%;
    font-size: 1.2rem;
    text-align: center;
    color: #fff;
    margin: 0;
    font-weight: 200;
    padding: 10px;
    z-index: 100;
    background: rgba(0, 0, 0, 0.5);
    opacity: 1; /* Initially hide the title */
    transition: opacity 0.3s ease;
}

.video-part-model{
    overflow: hidden;
    width: 100%;
    height: 45vh;
}

.play-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    transition: opacity 0.3s ease;
}

.video-div:hover .play-overlay {
    opacity: 1; /* Make the overlay visible on hover */
}

.video-div:hover .video-play {
    display: block; /* Show the play button on hover */
}

.video-div:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Adjust the alpha value for the desired darkness */
    opacity: 0;
    transition: opacity 0.3s ease; /* Add a transition effect to the overlay opacity */
  }
  
  .video-div:hover:before {
    opacity: 1; /* Make the overlay visible on hover */
  }

  

  @media (max-width: 600px) {
    .video-container {
        padding: 5rem 1rem;
    }

    
    .display-video {
        height: 30vh;
    }

    .video-part-display {
        
        grid-template-columns: repeat(1, 1fr) !important;
    
    }

    .each-video-title {
        font-size: 1rem;
    }

    .video-play {
        font-size: 2rem;
    }

    .video-part-model {
        height: 35vh;
    }
}