.header {
    position: fixed;
    display: flex;
    top: 0%;
    width: 100vw;
    height: 8vh;
    background: rgba(0, 0, 0, 0.32);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(2.3px);
    -webkit-backdrop-filter: blur(2.3px);
    z-index: 201;
}

.header-left-part {
    display: flex;
    align-items: center;
    gap: .5rem;
    justify-content: center;
    width: 50vw;
    height: 8vh;
    cursor: pointer;
}

.logo {
    height: 100%;
    width: 9vw;
    margin-bottom: .5%;
}

.logo-name {
    font-size: small;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.623);
    transition: color 0.5s ease;
}

.logo-name:hover {
    color: white;
}

.header-right-part {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50vw;
    gap: 20px;
    height: 8vh;
}

.header-right-part a {
    text-decoration: none;
    color: rgba(255, 255, 255, 0.623);
    transition: color 0.3s ease;
    cursor: pointer;
    font-size: small;
    text-transform: uppercase;
}

.header-right-part a:hover {
    color: white;
}

.active-link {
    color: rgba(255, 0, 0, 0.664) !important;
}

.responsive-nav{
    position: absolute;
    top: -120%;
    width: 100%;
    min-height: 100vh;
    background-color: #1C1C1C;
    opacity: 0;
    z-index: 200;
    transition: all cubic-bezier(0.19, 1, 0.22, 1)1s;
    
}

.sub-menu{
    display: none;
}

@media (max-width:600px) {

    
    .header-left-part {
        
    }

    .logo {
        height: 90%;
        width: 15vw;
    }

    .logo-name {
        display: none;
    }

    .header-right-part a {
        display: none;
    }

    .sub-menu{
        display: flex;
        font-size: 2.5rem;
        cursor: pointer;
        color: rgb(179, 30, 30);
    }

    .responsive-nav{
        
        display: flex;
        align-items: center;
        flex-direction: column;
        gap: 2rem;
        justify-content: center;
    }

    .responsive-nav.active {
        position: fixed;
        top: 0;
        opacity: 1;
      }
      

    .responsive-nav a{
        text-decoration: none;
    color: rgba(255, 255, 255, 0.623);
    transition: color 0.3s ease;
    cursor: pointer;
    font-size: small;
    text-transform: uppercase;

    }

    .responsive-nav a:hover {
        color: white;
    }
}