.home-container{
    width: 100vw;
    height: 100%;
    background-image: linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0.35),
        rgba(0, 0, 0, 0.6)
    ), url(../../assests/pics/L-4.jpg);
    
    display: flex;
    background-position: center;
}




.home-left-part{
    width: 50vw;
    height: 100%;
  
    display: flex;
    align-items: center;
    justify-content: center;
}

.home-experiance{
    position: relative;
    margin-top: 15%;
    margin-bottom: 10%;
    width: 70%;
    height: 70%;
    background-color: rgba(26, 25, 25, 0.7);
}

.h-experiance-image{
    width: 100%;
    height: 100%;
    
}



.h-experiance-image img{
    width: 100%;
    height: 100%;
    
    z-index: 2;
}

.h-experiance-top{
    position: absolute;
    left: 0;
    top: 0;
    transform: translate(-30px, -30px);
    z-index: 100;
}

.h-experiance-bottom{
    position: absolute;
    right: 0;
    bottom: 0;
    transform: translate(30px, 30px);
    z-index: 100;
}

.h-exp-box{
    width: 8rem;
    height: 6rem;
    background-color: rgb(53, 52, 52);
}

.home-experiance-text{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.home-experiance-text h1{
    color: rgb(160, 10, 10) ;
}

.home-experiance-text p{
    color: rgba(255, 255, 255, 0.774);
}

.h-exp-box1{
    width: 8rem;
    height: 6rem;
    background-color: rgb(29, 28, 28);
}


.home-right-part{
    width: 50vw;
    height: 100vh;
    display: flex;
    align-items: center;
}

.h-right-text-part{
    margin-top: 6%;
    margin-left: 4%;
    width:40vw;
    
    height: 70%;
    display: flex;
    flex-direction: column;

    gap: 3rem;
}

.h-right-text-part span{
    color: rgba(160, 10, 10, 0.671);
    letter-spacing: 2px;
}



.h-right-text-part h1{
    letter-spacing: 3px;
    color: rgba(255, 255, 255, 0.747);
    font-weight: 900;
    font-size: 3rem;
}

.h-right-text-part p{
    letter-spacing: 2px;
    color: rgba(255, 255, 255, 0.747);
}

.project-container{
    width: 100vw;
    height: 100%;
    background-color: rgb(24, 23, 23);
    display: flex;
    flex-direction: column;
    /* padding: 7rem 7rem; */
}

.project-title{
    width: 100vw;
    height: 15vh;
    background-color: black;
    display: flex;
    align-items: center;
    padding: 0rem 5rem;
}

.project-title h1{
    font-weight: 100;
    color: rgba(255, 255, 255, 0.747);
    font-size: 2.6rem;
}

.all-projects{
    width: 100vw;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
}

.single-project{
    position: relative;
    width: 25%;
    height: 100%;
    cursor: pointer;
    display: flex;
    flex-wrap: wrap;
}

.transparent-block {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgb(0, 0, 0);
    z-index: 100;
    opacity: 0; /* Set initial opacity to 0 */
    transition: opacity 0.3s ease; /* Add transition for opacity */
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.transparent-block h1{
    font-family: 'Island Moments';
    color: rgba(255, 255, 255, 0.747);
    font-size: 3rem;
}

.single-project img{
    width: 100%;
    height: 42rem;
    object-fit: fill;
    object-position: center;
}

.services-container{
    height: 100%;
    width: 100vw;
    background-color: rgb(24, 23, 23);
    display: flex;
    overflow-x: hidden;
    flex-direction: column;
    padding: 5rem 4rem;
}

.service-top-part{
    height: 50%;
    letter-spacing: 1px;
    width: 100%;
    margin-bottom: 2%;
    display: flex;
    flex-direction: column;
    text-align: center;
    gap: 2rem;
    align-items: center;
    justify-content: center;
    
}

.service-first{
    display: flex;
    flex-direction: column;
    text-transform: uppercase;
    gap: 1rem;
}

.service-first p{
    font-size: small;
    color: rgba(255, 0, 0, 0.685)
}

.service-first h1{
    font-size: 3rem;
    color: rgba(255, 255, 255, 0.747);
    font-weight: 100;
}

.service-desc{
    font-family: 'Island Moments';
    font-size: xx-large;
    color: rgba(255, 255, 255, 0.747);
}

.service-bottom-part{
    width: 100%;
    height: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    
}

.each-service{
    display: flex;
    width: 100%;
    height: 28.4rem;
    gap: 1.5rem;
    padding: 1rem 3rem;
    
}

.service-left{
    width: 50%;
    letter-spacing: 1px;
    height: 100%;
    background-color: rgb(41, 40, 40);
    display: flex;
    gap: 2rem;
    color: white;
    flex-direction: column;
    padding: 4rem 3rem;
}

.service-top{
    display: flex;
    flex-direction: column;
    text-transform: uppercase;
    gap: .5rem;
}

.service-top h1{
    letter-spacing: 2px;
    color: rgba(255, 255, 255, 0.747);
    font-weight: 500;
}

.each-service-desc{
    line-height: 1.7rem;
}

.service-right{
    width: 50%;
    height: 100%;
    background-color: black;
}

.service-right img{
    width: 100%;
    height: 100%;
}

.service-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 3%;
    color: rgba(255, 255, 255, 0.747);
    transform: translateX(350px);
    width: 40%;
    height: 3rem;
    border: none;
    cursor: pointer;
    outline: none;
    border-radius: 3px;
    font-size: 1.2rem;
    text-transform: uppercase;
    background-color: transparent;  /* Set to the initial background color or 'transparent' */
    border: 1px solid rgb(14, 82, 14);
    letter-spacing: 1.5px;
    transition: background-color 0.3s ease;  /* Add a transition for a smooth effect */
}

.service-btn:hover {
    background-color: rgb(14, 82, 14);
}

.responsive-service{
    display: none;
}

@media (max-width:600px) {
    .home-container {
        flex-direction: column;
        align-items: center;
    }

    .home-left-part,
    .home-right-part {
        width: 100%;
        height: auto;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .home-right-part {
        order: -1; /* Change the order to -1 to move it to the top */
    }

    .home-experience {
        width: 90%; /* Adjust to your preference */
        height: auto; /* Adjust to your preference */
        margin: 5%; /* Adjust to your preference */
    }

    .h-right-text-part {
        margin-top: 20%;
         /* Adjust to your preference */
        width: 90%; /* Adjust to your preference */
    }

    .h-right-text-part h1 {
        font-size: 2rem; /* Adjust to your preference */
        color: rgba(255, 255, 255, 0.747);
    }
      .single-project {
        width: 100%;
        height: auto;
        margin-bottom: .3rem; /* Adjust to your preference */
      }
    
      .transparent-block {
        height: 100%;
      }
    
      .transparent-block h1 {
        font-size: 1.5rem; /* Adjust to your preference */
        color: rgba(255, 255, 255, 0.747);
      }
      .services-container{
        display: none;
      }

      .responsive-service{
        padding: 1rem 0rem;
        gap: .5rem;
        background-color: black;
        display: flex;
        align-items: center;
        flex-direction: column;
        
    }

    .service-left.responsive-left{
        width: 95%;
        padding: 4rem 1.5rem;
    }

    .service-btn.responsive-btn{
        transform: translateX(0%);
        
    }
    
    .service-top-part.responsive-top{
        margin-bottom: 40px;
    }
}

