.footer-container{
    height: 100%;
    width: 100vw;
    background-color: rgb(46, 44, 44);
    display: flex;
    flex-direction: column;
    padding: 1rem;
    align-items: center;
    
    gap: 1.5rem;
}
.footer-tags{
    display: flex;
    text-align: center;
    gap: 10px;
}

.footer-tags p{
    letter-spacing: 2px;
    color: rgba(206, 24, 24, 0.836);
}

.footer-logo{
    width: 13rem;
    height: 9rem;
}

.social-media-links{
    display: flex;
    gap: 1rem;
}

.footer-icons{
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    align-items: center;
    cursor: pointer;
    color: rgba(255, 255, 255, 0.747);
    justify-content: center;
    background-color: transparent;  /* Set to the initial background color or 'transparent' */
    transition: background-color 0.3s ease;
    border: 1px solid rgb(14, 82, 14);
}

.footer-icons:hover {
    background-color: rgb(12, 107, 12);
}

.footer-logo img{
    width: 100%;
    height: 100%;
    
}

.footer-s-icon{
    font-size: 1.3rem;
}

.copyright{
    letter-spacing: 2px;
    color: rgba(206, 24, 24, 0.836);
}