.project-display-container {
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    /* align-items: center; */
    color: white;
    height: 100%;
    width: 100vw;
    padding-bottom: 2%;
    background-color: #1C1C1C;
    /* Set your desired background color */
}

.project-slider {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 8vh;
    align-items: center;
    height: 100vh;
    /* Set your desired width for the slider container */
    /* max-width: 1200px; */
    /* Set the maximum width if needed */
}

.swiper-slide {
    width: 100%;
    height: 70vh;
    /* Set your desired height for the slides */
    object-fit: cover;
}

.mySwiper2 {
    width: 100%;
}

.swiper-bottom {
    margin-top: 5px;
    width: 100%;
    gap: 5px !important;
    display: flex;
}

.swiper-button-next,
.swiper-button-prev {
    color: red;
}

.swiper-main-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.swiper-bottom .swiper-slide {
    width: 80px;
    /* Width of the slide */
    height: 115px;
    /* Height of the slide */
    object-fit: cover;
    /* Maintain aspect ratio; cover the area completely */
    margin-right: 5px;
    /* Space between slides */
}

.swiper-bottom img {
    width: 100%;
    /* Ensure the image fills the slide container */
    height: 100%;
    /* Ensure the image fills the slide container */
    object-fit: cover;
    /* Maintain aspect ratio; cover the area completely */
    object-position: center;
    /* Center the image within the slide */
}

.project-display{
        padding: .5rem;
        margin-top: -5%;
        display: grid;
        gap: 5px;
        grid-template-columns: repeat(4, 1fr);
        grid-auto-rows: minmax(350px, auto);
        grid-auto-flow:  dense;
}

.project-display div {
    background-size: cover;
    background-position: center;
    object-fit: cover;
    width: 100%;
    height: 100%;
    /* border: 1px solid white; */
    object-position: center;
}


.cat-links {
    position: relative;
}

